/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  border: 0;
  margin: 0;
  outline: 0;
  padding: 0;
  vertical-align: baseline;
}

html {
  -webkit-font-smoothing: none;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  overflow-x: hidden;
}

body {
  margin: 0;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 14px;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

a:active {
  outline: 0;
}

a,
a:visited,
a:hover,
a:focus,
a:-webkit-any-link {
  text-decoration: none;
}

a {
  color: #0F8B8D;
}

figure {
  margin: 0;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

input[type=search], input[type=text], input[type=password] {
  padding: 8px;
  flex-grow: 1;
  min-width: 3em;
  border: 1px solid #949494;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}

input[type=submit],
button[type=submit] {
  background: #f7f7f7;
  border: 1px solid #ccc;
  padding: 8px 0.625em;
  color: #32373c;
  margin-left: 0.625em;
  word-break: normal;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  cursor: pointer;
}

.site-header {
  background: rgb(22, 22, 22);
  position: fixed;
  visibility: hidden;
  top: -52px;
  z-index: 99;
  min-width: -webkit-fill-available;
  min-height: 52px;
  transition: ease-in-out 0.3s;
}
.site-header.active {
  top: 0;
  visibility: visible;
}
.site-header .site-branding .header-color {
  color: #ff4444;
  position: relative;
  left: -8px;
}
@media (max-width: 550px) {
  .site-header .site-branding .header-color {
    font-size: 15px;
    top: 16.5px;
    left: -116px;
    margin-left: 0px;
  }
}
.site-header img {
  margin-left: 20px;
  padding-top: 12px;
}
@media (max-width: 410px) {
  .site-header img {
    max-width: 100px;
  }
}
.site-header a {
  padding-top: 12px;
  display: block;
  font-size: 20px;
  font-weight: 500;
  font-family: "Raleway", sans-serif;
  letter-spacing: 5px;
  text-transform: uppercase;
  text-decoration: none;
  color: white;
  line-height: 30px;
}
@media (max-width: 550px) {
  .site-header a {
    font-size: 15px;
    top: -5px;
    position: relative;
    font-weight: 700;
    padding-top: 9px;
  }
}
.site-header a:visited {
  color: white;
}